import React from 'react';
import Link from 'gatsby-link';
import sortBy from 'lodash/sortBy';
import get from 'lodash/get';
import include from 'lodash/includes';
import Paper from 'material-ui/Paper';
import Grid from 'material-ui/Grid';

import GridItem from '../components/grid-item';
import PostExcerpt from '../components/post-excerpt';

export default class Index extends React.Component {

  getPostList() {
    const postList = [];
    this.props.data.allMarkdownRemark.edges.forEach((edge) => {
      postList.push({
        excerpt: edge.node.excerpt,
        slug: edge.node.fields.slug,
        path: edge.node.frontmatter.path,
        title: edge.node.frontmatter.title,
        date: edge.node.frontmatter.date,
        cover: edge.node.frontmatter.cover
          ? edge.node.frontmatter.cover.childImageSharp.responsiveResolution.src
          : null,
      });
    });
    return postList;
  }
  render() {
    const postList = this.getPostList();
    const { title } = this.props.data.site.siteMetadata;
    return (
      <div>
        <Grid container spacing={0}>
          {postList.map(page => (
            <GridItem key={page.path} page={page} PostType={PostExcerpt} />
          ))}
        </Grid>
      </div>
    );
  }
}

/* eslint no-undef: "off"*/
export const pageQuery = graphql`
  query IndexQuery {
  site {
    siteMetadata {
      title
    }
  }
  allMarkdownRemark(
      limit: 2000,
      sort: { fields: [frontmatter___date], order: DESC },
    ) {
      edges {
        node {
          excerpt(pruneLength: 250)
          fields {
            slug
          }
          frontmatter {
            title
            date
            path
            parent
            cover {
              childImageSharp {
                responsiveResolution(
                  width: 600,
                ) {
                src
               }
              }
            }
          }
        }
      }
    }
}
`;



